<template>
    <div class="registration">
        <div class="wrapper">
            <div class="now-page">
                <p>Главная > <span>Оформление заказа</span></p>
            </div>
            <h2 class="h30">Оформление заказа</h2>
            <hr>
            <div class="registration-content">
                <div>
                    <p>Дата и время доставки</p>
                    <div class="registration-select">
                        <div style="max-height: 19px;" class="custom-select dateSelect">
                            <div style="position: relative;">
                                <span style="position: absolute;">{{one}}</span>
                            </div>
                            <div style="position: relative;">
                                <input :min="this_day" v-model="one"  class="dateInput" type="date">
                            </div>
                        </div>
                        <div class="custom-select" style="background: #FAFAFB; border: #FAFAFB; cursor:pointer;">
                            <div>
                                <span @click="selectTwo = !selectTwo">{{two}}</span>
                            </div>
                            <div style="position: relative;" v-show="selectTwo" v-for="(hour, index) in hours" :key="index">
                                <label @click="two = hour, selectTwo = false">
                                    {{hour}}
                                    <input v-model="selHour" :value="hour" name="selHour" type="radio">
                                    <span class="radiobtn"></span>
                                </label>
                            </div>
                            <i class="fas fa-caret-down"></i>
                        </div>
                    </div>
                    <p>Адрес доставки</p>
                    <div class="address">
                        <div>
                            <span style="width: 280px; cursor: pointer;" @click="selectThree = !selectThree">{{address}}</span>
                        </div>
                        <div class="selectAddress" v-if="selectThree">
                            <div @click="address='Указать другой адрес'">
                                <label @click="choseAddress()">
                                    <input v-model="selAddress" :value="null" name="selAddress" type="radio">
                                    <span>Указать другой адрес</span>
                                    <span class="radiobtn"></span>
                                </label>
                            </div>
                            <div @click="address = 'Алматы ' + item.street + item.home + ' кВ ' + item.apartment" v-for="(item, index) in USER_AUTH.addresses" :key="index">
                                <label @click="choseAddress()">
                                    <input v-model="selAddress" :value="item.id" name="selAddress" type="radio">
                                    <span>Алматы, {{item.street}} {{item.home}}, кВ{{item.apartment}}</span>
                                    <span class="radiobtn"></span>
                                </label>
                            </div>
                        </div>
                        <i class="fas fa-caret-down"></i>
                    </div>
                </div>
                <div>
                    <p>Способ оплаты</p>
                    <div class="pay" v-for="(hour, index) in payType" :key="index">
                        <div>
                            <label>
                                {{hour}}
                                <input v-model="selectFour" :value="index" name="selectFour" type="radio">
                                <span class="radiobtn"></span>
                            </label>
                        </div>
                    </div>
                </div>
            </div>
            <div class="registration-content2">
                <div>
                    <p>Указать другой адрес</p>
                    <input :disabled="selAddress!==null" v-model="street" class="max-input" type="text" placeholder="Улица"><br>
                    <input :disabled="selAddress!==null" v-model="home" class="mini-input" type="text" placeholder="Дом">
                    <input :disabled="selAddress!==null" v-model="apartment" class="mini-input" style="margin-right: 0;" type="text" placeholder="Квартира">
                </div>
                <div>
                    <h2 class="h30">Итого: {{price}} тенге</h2>
                    <button @click="regisrt()">Оформить заказ</button>
                </div>
            </div>
        </div>
        <mini-banner></mini-banner>
        <modal-box v-if="box" :boxText="boxTitle" :typeInfo="typeInfo"></modal-box>
    </div>
</template>

<script>
    import MiniBanner from '@/components/banner/MiniBanner'
    import ModalBox from '@/components/ModalBox'
    import {mapGetters} from 'vuex'
    export default {
        data() {
            return {
                day: '',
                hours: ['C 10:00 до 12:00', 'C 13:00 до 15:00', 'C 16:00 до 18:00'],
                arr: [],
                payType: ['Наличными курьеру', 'Картой'],
                address: 'Выберите адрес доставки',
                selectOne: false,
                selectTwo: false,
                selectThree: false,
                selectFour: false,
                one: '',
                two: 'C 10:00 до 12:00',
                three: '',
                four: 'Наличными курьеру',
                selAddress: '',
                selHour: '',
                box: false,
                typeInfo: '',
                boxTitle: 'Попробуйте еще раз',
                street: '',
                home: '',
                apartment: '',

                price: localStorage.video_total_price
            }
        },
        computed: {
            ...mapGetters([
                'USER_AUTH',
                'BASKET_BUY'
            ]),
            this_day(){
                let now = new Date().toISOString().slice(0,10);
                return now;
            }
        },
        methods: {
            regisrt(){
                console.log('asdfsdf')
                let formData = new FormData();
                formData.append('delivery_date', this.one)
                formData.append('delivery_hour', this.selHour)
                formData.append('order_type', 'order')
                formData.append('pay_type', parseInt(this.selectFour)+1)
                if(this.selAddress !== null) formData.append('address_id', this.selAddress)
                if(this.selAddress === null) {
                    formData.append('street', this.street)
                    formData.append('home', this.home)
                    formData.append('apartment', this.apartment)
                }
                formData.append('installation', 0)
                this.$store.dispatch('POST', [
                    formData,
                    { name: 'basket_buy' }
                ])
                setTimeout(() => {
                    console.log(this.$store.getters.BASKET_BUY)
                    if(this.$store.getters.BASKET_BUY === 200) {
                        setTimeout(() => {
                            this.$store.dispatch('GET_EXCEPTION', 'basket_list')
                        }, 200);
                        this.$router.push({ path: '/thanks'})
                    }
                    else{
                        this.boxTitle = this.$store.getters.BASKET_BUY || 'Попробуйте еще раз';
                        this.typeInfo = 'warn';
                        this.box = true;
                        setTimeout(() => {
                            this.box = false;
                        }, 1100);
                    }
                }, 1000);
            },
            choseAddress(){
                setTimeout(() => {
                    this.selectThree = false
                }, 100);
            }
        },
        components: {
            MiniBanner,
            ModalBox
        },
        beforeCreate () {
            this.$store.dispatch('POST', [
                {},
                { name: 'user_auth' }
            ])
        },
    }
</script>

<style scoped>
    .radiobtn {
        position: absolute;
        top: 5px;
        right: 10px;
        height: 20px;
        width: 20px;
        background-color: #fff;
        border: 1px solid #D9D9D9;
    }
    .radiobtn:after {
        content: "";
        position: absolute;
        display: none;
    }
    div input:checked ~ .radiobtn:after {
        display: block;
    }
    div .radiobtn:after {
        top: 3px;
        left: 3px;
        width: 14px;
        height: 14px;
        background: #2196F3;
    }
    label input{
        position: absolute;
        opacity: 0;
        visibility: hidden;
    }
    
    .pay .radiobtn{
        top: 10px;
    }
    .dateInput{
        margin-top: -20px;
        width: 270px;
        border: none;
        outline: none;
    }
</style>