<template>
    <div class="product" v-if="product">
        <div class="wrapper">
            <div class="now-page">
                <p>Главная > <span>Камера наблюдения - HIKVISON </span> <a>Купить в один клик</a></p>
            </div>
            <div class="product-content">
                <div class="product-content-block1">
                    <div class="product-block1-images">
                        <div v-for="(item, index) in product.images" :key="index">
                            <img @click="openImg(index)" :src="url + item" alt="">
                        </div>
                    </div>
                    <div class="product-block1-img">
                        <div :style="{backgroundColor: textColor(product.product_status)}" class="products-status">
                            {{product.product_status}}
                        </div>
                        <img :src="images()" alt="">
                    </div>
                </div>
                <div class="product-content-block2">
                    <h2>{{product.name}}</h2>
                    <hr>
                    <div class="product-content-block2-flex">
                        <div class="product-block2-flex-block1">
                            <div class="product-block2-count">
                                <button @click="count--" :disabled="count<=1">-</button>
                                {{count}}
                                <button @click="count++" :disabled="count>=10">+</button>
                            </div>
                            <div class="product-block2-price">
                                <small>Цена</small>
                                <p>{{product.price}} тенге</p>
                            </div>
                        </div>
                        <div class="product-block2-flex-block2">
                            <div class="product-favorite"  @click="favoriteClick(product.id, product.in_favorite)">
                                <i class="fa-heart" :class="product.in_favorite===true ? 'fas' : 'far'"></i>
                            </div>
                            <button @click="addProduct(product.id, count)">Купить</button>
                        </div>
                    </div>
                    <hr>
                    <div class="product-about">
                        <p @click="desc = 'description'" :class="{about: desc === 'description'}">Описание</p>
                        <p @click="desc = 'characteristic'" :class="{about: desc === 'characteristic'}">Характеристика</p>
                        <p @click="desc = 'once'" :class="{about: desc === 'once'}">Купить в один клик</p>
                    </div>
                    <hr>
                    <div class="product-about">
                        <div v-if="desc === 'description'">
                            <p v-html="product.description"></p>
                        </div>
                        <div v-if="desc === 'characteristic'">
                            <p v-html="product.characteristic"></p>
                        </div>
                        <div v-if="desc === 'once'" class="onceClick">
                            <h3>Купить в один клик</h3>
                            <p>
                                Товарищи! консультация с широким активом позволяет выполнять важные 
                                задания по разработке соответствующий условий активизации. С другой 
                                стороны укрепление и развитие структуры требуют определения и уточнения 
                                существенных финансовых и административных условий.
                            </p>
                            <div class="onceClick-block">
                                <small>Введите ваше имя:</small>
                                <small>Введите свой номер:</small>
                            </div>
                            <div class="onceClick-block">
                                <input v-if="USER_AUTH === []" v-model="name" type="text" placeholder="Ваше имя">
                                <input v-if="USER_AUTH === []" v-model="phone" type="text" placeholder="Ваш номер">
                                <p v-if="USER_AUTH !== []">{{USER_AUTH.name}}</p>
                                <p v-if="USER_AUTH !== []">{{USER_AUTH.phone}}</p>
                            </div>
                            <div class="onceClick-block">
                                <label>
                                    <input v-model="installation" type="checkbox">
                                    Заказать&nbsp;с&nbsp;установкой
                                </label>
                                <button @click="onceClick()">Купить</button>
                            </div>                            
                        </div>
                    </div>
                </div>
            </div>
            <app-similar @counter="count=1" :id="id"></app-similar>
        </div>
        <mini-banner></mini-banner>
        <modal-box v-if="box" :boxText="boxTitle" :typeInfo="typeInfo"></modal-box>
    </div>
</template>

<script>
    import MiniBanner from '@/components/banner/MiniBanner'
    import MainProduct from '@/components/products/mainProduct'
    import {mapGetters} from 'vuex'
    import AppSimilar from '@/components/Similar'
    import ModalBox from '@/components/ModalBox'
    export default {
        props: {
            'id': {
                type: String,
                required: true,
                default: 1
            }
        },
        data() {
            return {
                favoriteActive: false,
                favorite: '',
                count: 1,
                image: '',
                desc: 'description',
                box: false,
                boxTitle: '',
                typeInfo: '',
                url: 'http://194.4.58.57/',
                installation: false,
                name: '',
                phone: ''
            }
        },
        computed: {
            ...mapGetters([
                'FAVORITE_LIST',
                'BUYS_WITH',
                'PRODUCT_SHOW',
                'USER_AUTH'
            ]),
            product() {
                let products = this.PRODUCT_SHOW;
                return products
            },
            similar(){
                setTimeout(() => {
                    this.$store.dispatch('POST_EX1', [
                        {
                            id: this.id
                        },
                        {
                            name: 'buys_with'
                        }
                    ])
                }, 200);
            },
        },
        methods: {
            favoriteClick(id, bool){
                if(localStorage.login === true || localStorage.login === 'true'){
                    this.$store.dispatch('POST', [
                        {
                            product_id: id
                        },
                        {
                            name: 'favorite'
                        }
                    ])
                    this.$store.dispatch('GET_EXCEPTION', 'favorite_list')
                    this.again();
                    if(bool) this.recurse('Вы убрали товар из ИЗБРАННЫХ!', 'warn');
                    else if(!bool) this.recurse('Вы добавили товар в ИЗБРАННЫХ!', 'success');
                }
                else{
                    this.recurse('Вы не авторизованы', 'warn');
                }
            },
            addProduct(id, count){
                if(localStorage.login === true || localStorage.login === 'true'){
                    this.$store.dispatch('POST', [
                        {
                            product_id: id,
                            count: '1'  
                        },
                        { name: 'basket_add' }
                    ])
                    this.recurse('Вы добавили товар в корзину!', 'success');
                    setTimeout(() => {
                        this.$store.dispatch('GET_EXCEPTION', 'basket_list')
                    }, 200);
                }
                else{
                    this.recurse('Вы не авторизованы', 'warn');
                }
            },
            again(){
                this.$store.dispatch('GET', [
                    {
                        id: this.id
                    },
                    {
                        name: 'product_show'
                    }
                ])
            },
            openImg(id){
                this.image = this.product.images[id]
            },
            textColor(status){
                if(status === 'В наличии') return '#32A914'
                else if(status === 'Мало') return '#FF4858'
                else if(status === 'Средне') return '#FFAB48'
                return '#A91414'
            },
            recurse(title, type){
                this.boxTitle = title;
                this.typeInfo = type;
                this.box = true;
                setTimeout(() => {
                    this.box = false;
                }, 1100);
            },
            images(){
                // console.log(this.product.images.length)
                if(this.product.images.length !== 0){
                    return this.url + (this.image || this.product.images[0])
                }
                else{
                    return '../default.jpg'
                }
            },
            onceClick(){
                if(this.name === '') this.name = this.USER_AUTH.name;
                if(this.phone === '') this.phone = this.USER_AUTH.phone;
                // this.$store.dispatch('POST', [
                //     {
                //         'installation': this.installation ? 1 : 0,
                //         'phone': this.phone,
                //         'name': this.name,
                //         'product_id': this.id,
                //         'count': this.count
                //     },
                //     { name: 'once_click' }
                // ])
                this.boxTitle = 'Спасибо за заявку! Скоро с вами свяжемся';
                this.typeInfo = 'success';
                this.box = true;
                setTimeout(() => {
                    this.box = false;
                    this.desc = 'description';
                }, 2000);
            }
        },
        mounted () {
            this.similar;
            this.product;
        },
        components: {
            MiniBanner,
            MainProduct,
            AppSimilar,
            ModalBox
        },
        created () {
            this.again();
            this.$store.dispatch('POST', [
                {},
                { name: 'user_auth' }
            ])
        },
    }
</script>

